* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,
html {
  height: 100%;
  font-family: 'Poppins', sans-serif;
}
a {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  line-height: 1.7;
  color: #666;
  margin: 0;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
}
a:focus {
  outline: none !important;
}
a:hover {
  text-decoration: none;
  color: #a64bf4;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  line-height: 1.7;
  color: #666;
  margin: 0;
}
ul,
li {
  margin: 0;
  list-style-type: none;
}
input {
  outline: none;
  border: none;
  /* color: #fff !important; */
}
textarea {
  outline: none;
  border: none;
}
textarea:focus,
input:focus {
  border-color: transparent !important;
}
.input100 {
  color: #fff !important;
}
input:focus::placeholder,
input::placeholder,
.label_input100 {
  color: #fff !important;
}
input:focus::_webkit_input_placeholder {
  color: #fff;
}
input:focus:_moz_placeholder {
  color: #fff;
}
input:focus::_moz_placeholder {
  color: #fff;
}
input:focus:_ms_input_placeholder {
  color: #fff;
}
textarea:focus::_webkit_input_placeholder {
  color: #fff;
}
textarea:focus:_moz_placeholder {
  color: #fff;
}
textarea:focus::_moz_placeholder {
  color: #fff;
}
textarea:focus:_ms_input_placeholder {
  color: #fff;
}
input::_webkit_input_placeholder {
  color: #fff;
}

input::_moz_placeholder {
  color: #fff;
}

textarea::_webkit_input_placeholder {
  color: #adadad;
}

textarea::_moz_placeholder {
  color: #fff;
}

button {
  outline: none !important;
  border: none;
  background: 0 0;
}
button:hover {
  cursor: pointer;
}
iframe {
  border: none !important;
}
