.limiter {
  width: 100%;
  margin: 0 auto;
}
.container_login100 {
  width: 100%;
  min-height: 100vh;
  display: _webkit_box;
  display: _webkit_flex;
  display: _moz_box;
  display: _ms_flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 15px;
  background-repeat: no_repeat;
  background-position: center;
  background-size: cover;
  background-image: url(../../../assets/images/bg.jpg);
  position: relative;
}
.container_login100:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.wrap_login100 {
  max-width: 600px;
  background: rgb(26 106 39 / 81%);
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}
.login100_form {
  width: 100%;
}
.login100_form_title {
  display: block;
  font-family: 'Poppins', sans-serif;
  font-size: 39px;
  color: rgba(255, 255, 255, 1);
  line-height: 1.2;
  text-align: center;
  position: relative;
  z-index: 999;
}
.wrap_input100 {
  width: 100%;
  position: relative;
  border-bottom: 2px solid #d9d9d9;
}
.label_input100 {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  color: #333;
  line-height: 1.5;
  padding-left: 7px;
  position: relative;
  z-index: 999;
}
.input100 {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  color: #333;
  line-height: 1.2;
  display: block;
  width: 100%;
  height: 55px;
  background: 0 0;
  padding: 0 7px 0 7px;
}
.focus_input100 {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: auto;
  left: 0;
  pointer-events: none;
}
.focus_input100::after {
  content: attr(data_symbol);
  font-family: Material-Design-Iconic-Font;
  color: #adadad;
  font-size: 22px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: calc(100% - 20px);
  bottom: 0;
  left: 0;
  padding-left: 13px;
  padding-top: 3px;
}
.focus_input100::before {
  content: '';
  display: block;
  position: absolute;
  bottom: _2px;
  left: 0;
  width: 0;
  height: 2px;
  background: #7f7f7f;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
.input100:focus + .focus_input100::before {
  width: 100%;
}
.has_val.input100 + .focus_input100::before {
  width: 100%;
}
.input100:focus + .focus_input100::after {
  color: #a64bf4;
}
.has_val.input100 + .focus_input100::after {
  color: #a64bf4;
}
.container_login100_form_btn {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}
.wrap_login100_form_btn {
  width: 100%;
  display: block;
  position: relative;
  z-index: 1;
  border-radius: 25px;
  overflow: hidden;
  margin: 0 auto;
  box-shadow: 0 5px 30px 0 rgba(3, 216, 222, 0.2);
  -moz-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, 0.2);
  -webkit-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, 0.2);
  -o-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, 0.2);
  -ms-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, 0.2);
}
.login100_form_bgbtn {
  position: absolute;
  z-index: -1;
  width: 300%;
  height: 100%;
  background: #a64bf4;
  background: -webkit-linear-gradient(to right, #00dbde, #fc00ff, #00dbde, #fc00ff);
  background: -o-linear-gradient(to right, #00dbde, #fc00ff, #00dbde, #fc00ff);
  background: -moz-linear-gradient(to right, #00dbde, #fc00ff, #00dbde, #fc00ff);
  background: linear-gradient(to right, #00dbde, #fc00ff, #00dbde, #fc00ff);
  top: 0;
  left: -100%;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
.login100_form_btn {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  color: #fff;
  line-height: 1.2;
  text-transform: uppercase;
  display: _webkit_box;
  display: _webkit_flex;
  display: _moz_box;
  display: _ms_flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  width: 100%;
  height: 50px;
}
.wrap_login100_form_btn:hover .login100_form_bgbtn {
  left: 0;
}
.validate_input {
  position: relative;
}
.alert_validate::before {
  content: attr(data_validate);
  position: absolute;
  max-width: 70%;
  background-color: #fff;
  border: 1px solid #c80000;
  border-radius: 2px;
  padding: 4px 25px 4px 10px;
  bottom: calc((100% _ 20px) / 2);
  -webkit-transform: translateY(50%);
  -moz-transform: translateY(50%);
  -ms-transform: translateY(50%);
  -o-transform: translateY(50%);
  transform: translateY(50%);
  right: 2px;
  pointer-events: none;
  font-family: 'Poppins', sans-serif;
  color: #c80000;
  font-size: 13px;
  line-height: 1.4;
  text-align: left;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.4s;
  -o-transition: opacity 0.4s;
  -moz-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.alert_validate::after {
  content: '\f06a';
  font-family: FontAwesome;
  display: block;
  position: absolute;
  color: #c80000;
  font-size: 16px;
  bottom: calc((100% _ 20px) / 2);
  -webkit-transform: translateY(50%);
  -moz-transform: translateY(50%);
  -ms-transform: translateY(50%);
  -o-transform: translateY(50%);
  transform: translateY(50%);
  right: 8px;
}
.alert_validate:hover:before {
  visibility: visible;
  opacity: 1;
}
@media (max_width: 992px) {
  .alert_validate::before {
    visibility: visible;
    opacity: 1;
  }
}
.login100_social_item {
  font-size: 25px;
  color: #fff;
  display: _webkit_box;
  display: _webkit_flex;
  display: _moz_box;
  display: _ms_flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 5px;
}
.login100_social_item:hover {
  color: #fff;
  background-color: #333;
}
@media (max_width: 576px) {
  .wra ogin100 {
    padding-left: 15px;
    padding-right: 15px;
  }
}

