.headerInner {
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
  box-shadow: 0 8px 4px -4px #e9e9e9;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
}

.headLeft,
.menuanme {
  float: left;
  width: 20%;
  box-sizing: border-box;
}
.headRight {
  float: left;
  width: 80%;
  box-sizing: border-box;
  padding-left: 20px;
}
.userprofile {
  float: left;
  width: 100%;
  box-sizing: border-box;
  padding-left: 20px;
}
.clear {
  clear: both;
}

.userprofile {
  text-align: right;
  margin-top: 10px;
}
.dropdownNav {
  /* display: none; */
  position: absolute;
  right: 60px;
  background-color: #fff;
  padding: 10px;
  box-shadow: 0px 0px 2px #555777;
  width: 100px;
  top: 100%;
}
.dropdownNav ul li {
  display: block;
  text-align: left;
}
.dropdownNav ul li a,
.dropdownNav ul li button {
  font-size: 14px;
  color: #000;
  font-weight: 500;
}

.userinner {
  padding: 0px 20px;
  margin: 0px 20px;
}
.userinner p,
.userinner img,
.userinner span {
  display: inline-block;
  cursor: pointer;
}
.userinner span {
  padding-left: 8px;
  vertical-align: text-top;
  transform: rotate(90deg);
  font-weight: 500;
  font-size: 18px;
}
.userinner img {
  max-width: 100%;
  width: 30px;
  text-align: left;
  object-fit: contain;
  vertical-align: middle;
}
.userinner p {
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 600;
  vertical-align: text-top;
  margin-right: 5px;
  margin-top: 2px;
}
.menuanme h3 {
  margin: 0;
}
.headLeft img {
  max-width: 100%;
  width: 75px;
}
